@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

*, html {
    font-family: 'Poppins', sans-serif;
    border: 0;
    margin: 0;
    padding: 0;
}

.landing {
    padding: 0;
    margin: 0;
    width: 100vw;
    height: 1vw;
    color: white;
    background-image: url('./f12.jpg');
    background-size: cover;
    overflow: hidden;

    position: absolute;
    padding-left: 10%;
    padding-top: 55%;
}

.landing h1 {
    font-size: 4rem;
    line-height: 76%;
}

.landing h2 {
    font-size: 3.5rem;
}